<template>
  <!-- <b-container class="mt-5 bv-example-row mx-auto"> -->
  <b-container class="mt-1 mx-auto">
    <b-breadcrumb :items="itemsBreadCrumb"> </b-breadcrumb>

    <div class="container p-3 mb-3 bg-primary rounded-0 text-white">
      <h1>Colaboradores</h1>
      <p>
        Nesta seção você poderá visualizar todos os colaboradores do salão, os
        serviços oferecidos por cada um.
      </p>
    </div>

    <b-card
      v-for="colab in colaboradores"
      :title="colab.nome"
      :sub-title="colab.titulo"
      :img-src="`https://picsum.photos/600/300/?image=${colab.imgNum}`"
      img-alt="Image"
      img-top
      tag="article"
      style="max-width: 20rem"
      class="mb-3 mx-auto mx-sm-auto float-md-left mx-md-2 shadow"
    >
      <b-button
        v-b-modal="`modal-${colab.chave}`"
        href="#"
        variant="outline-primary"
        class="mt-3 mr-2"
        >Visualizar Serviços</b-button
      >
      <b-modal
        ok-only
        ok-title="Fechar"
        ok-variant="secondary"
        scrollable
        :id="`modal-${colab.chave}`"
        size="xl"
        :title="`Serviços de ${colab.nome}`"
        @hidden="buscaServico = null"
      >
        <b-container class="sticky-top shadow p-3 bg-secondary border-rounded">
          <b-form-input
            type="search"
            placeholder="Digite para buscar... (ex.: corte)"
            class="mb-0 shadow"
            v-model="buscaServico"
          ></b-form-input>
        </b-container>

        <hr />

        <b-card
          v-for="servico in servicosFiltrados"
          :title="servico.nome"
          sub-title="R$ 35,00"
          class="mb-2 rounded-sm float-lg-left mx-lg-1 float-xl-left mx-xl-2 shadow-sm"
          title-tag="h6"
          body-border-variant="success"
          body-text-variant="primary"
        >
          <b-badge
            pill
            variant="secondary"
            class="ml-1"
            v-for="cat in servico.categoria"
            >{{ cat }}</b-badge
          ><br />
        </b-card>
        <!-- <p :class="computedVisibility">
          Não há serviços com o nome
          <span class="font-weight-bold">"{{ buscaServico }}"</span>.
        </p> -->
        <div
          :class="computedVisibility"
          class="alert alert-warning"
          role="alert"
        >
          Não há serviços com o nome
          <span class="font-weight-bold">"{{ buscaServico }}"</span> para o
          colaborador {{ colab.nome }}. <br />
          <a href="" @click.prevent="limparBusca">Limpar busca</a>.
        </div>
      </b-modal>

      <b-button
        v-b-modal="`modalAgendar-${colab.chave}`"
        href="#"
        variant="primary"
        class="mt-3"
        >Agendar</b-button
      >
      <b-modal
        scrollable
        :id="`modalAgendar-${colab.chave}`"
        size="md"
        :title="`Agendar com ${colab.nome}`"
        @hidden="mostrarDataEscolhida"
      >
        <b-calendar
          block
          v-model="valueCalendarAgendar"
          :min="min"
          :max="max"
          locale="pt-BR"
        ></b-calendar>
        <br />
        <!-- <b-icon icon="chevron-bar-down"></b-icon> -->
        <b-icon-arrow-down class="h1 mt-3"></b-icon-arrow-down>
      </b-modal>
    </b-card>
  </b-container>
</template>

<script>
export default {
  data() {
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    // 15th two months prior
    const minDate = new Date(today);
    // minDate.setMonth(minDate.getMonth() - 2);
    // minDate.setDate(15);
    // 15th in two months
    const maxDate = new Date(today);
    maxDate.setMonth(maxDate.getMonth() + 2);
    maxDate.setDate(15);
    return {
      valueCalendarAgendar: null,
      min: minDate,
      max: maxDate,
      buscaServico: null,
      colaboradores: [
        { chave: "sol", nome: "Sol", titulo: "Cabeleireira", imgNum: "25" },
        { chave: "paulo", nome: "Paulo", titulo: "Babeiro", imgNum: "20" },
        { chave: "matheus", nome: "Matheus", titulo: "Barbeiro", imgNum: "15" },
        { chave: "jaisson", nome: "Jaisson", titulo: "Barbeiro", imgNum: "30" },
      ],

      servicoOptions: [
        {
          nome: "CORTE SOCIAL TESOURA (FEMININO OU MASCULINO)",
          categoria: ["cabelo"],
        },
        {
          nome: "CORTE DISFARÇADO/DEGRADÊ",
          categoria: ["cabelo"],
        },
        {
          nome: "CORTE DISFARÇADO + SOBRANCELHA COM GILETE",
          categoria: ["cabelo", "sobrancelha"],
        },
        {
          nome: "CORTE SOCIAL + SOBRANCELHA COM PINÇA",
          categoria: ["cabelo", "sobrancelha"],
        },
        {
          nome: "SOBRANCELHA COMUM (PINÇA)",
          categoria: ["sobrancelha"],
        },
        {
          nome: "Buço",
          categoria: ["buço"],
        },
        {
          nome: "Corte + Barba",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "CORTE FEMININO (CURTO) + SOBRANCELHA HENNA ()",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "CORTE FEMININO + SOBRANCELHA COMUM (GILETE)",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "SOBRANCELHA COMUM + BUÇO",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "CORTE TODO COM MÁQUINA",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "SOBRANCELHA masculina GILETE",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "CORTE FEMININO LONGO (NÃO LAVAMOS E NEM SECAMOS)",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "CORTE ESTILO AFRO",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "CORTE DISFARÇADO + SOBRANCELHA COM PINÇA",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "SOBRANCELHA HENNA + BUÇO",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "Corte e barba",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "Corte fem+sobrancelha hena",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "Corte e barba",
          categoria: ["barba", "cabelo"],
        },
        {
          nome: "Corte social(feminino e masc so tesoura",
          categoria: ["barba", "cabelo"],
        },
      ],
      itemsBreadCrumb: [
        {
          text: "Home",
          href: "/",
        },
        {
          text: "Colaboradores",
          href: "/",
          active: true,
        },
      ],
    };
  },
  methods: {
    limparBusca() {
      this.buscaServico = "";
    },
    doSomethingOnHidden() {
      alert("OI");
    },
    mostrarDataEscolhida() {
      console.log("data escolhida: " + this.valueCalendarAgendar);
    },
  },
  computed: {
    servicosFiltrados() {
      if (this.buscaServico) {
        return this.servicoOptions.filter((servOpt) => {
          return servOpt.nome.toLowerCase().includes(this.buscaServico);
        });
      } else {
        return this.servicoOptions;
      }
    },
    computedVisibility() {
      if (this.buscaServico && this.servicosFiltrados.length == 0) {
        return "d-block";
      } else {
        return "d-none";
      }
    },
  },
  mounted() {},
};
</script>

<style></style>
